import { on, off } from 'util/dom';
// import { debounce } from 'util/debounce';

export default {
  mounted() {
    on(window, 'resize', this.updateSize);

    // mounted
    this.$nextTick(() => {
      this.updateSize();
    });
  },

  destroyed() {
    off(window, 'resize', this.updateSize);
  }

  // methods: {
  //   updateSize() {}
  // }
};
